<template>
    <div>
      <div class="menu-button" @click="toggleMenu">
            <i class="el-icon-s-unfold"></i>
        </div>
      <div class="menu-container" :class="{ 'menu-open': isMenuOpen }">
        <!-- 菜单内容 -->
        <ul>
            <li>
                <router-link to="/company">企业介绍 </router-link>   
            </li>
            <li>
                <a href="" @click="noLinkClick">个人简介</a> 
            </li>
            <li>
                <router-link to="/identity">身份</router-link>     
            </li>
            <li>
                <a href=""  @click="noLinkClick">品牌展示</a>
            </li>
            <li>
                <a href="java" @click="noLinkClick">微博</a>
            </li>
            <li>
                <a href="http://www.bighandz.co.jp/" target="_blank">手机官网</a> 
            </li>
            <li> 
                <router-link to="/map">地址导航 </router-link>     
            </li>
        </ul>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        isMenuOpen: false,
      };
    },
    methods: {
      toggleMenu() {
        this.isMenuOpen = !this.isMenuOpen;
      },
      
      noLinkClick(event) {
            // 阻止默认跳转行为
            event.preventDefault(); 
        },
    },
  };
  </script>
  
  <style scoped>

  
  .menu-button {
    position: fixed;
    top: 4px;
    right: 10px;
    background-color: #fff;
    padding: 6px;
    border-radius: 5px;
    cursor: pointer;
    z-index: 1;
    color: #000;
    font-size: 22px;
    font-weight: normal;
    box-shadow: #e0e0e0 0 0 3px 1px;
    width: 22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .menu-container {
    position: fixed;
    top: 0;
    right: -300px; /* 初始时隐藏菜单 */
    width: 300px;
    height: 100%;
    background-color: #fff;
    transition: right 0.3s ease-in-out;
    z-index: 0;
  }
  
  .menu-open {
    right: 0; /* 打开菜单时右侧移动到0位置 */
  }
  
  .menu-container ul {
    list-style: none;
    padding: 60px 0;
  }
  
  .menu-container li {
    padding: 6px 15px;
    border-bottom: 1px solid #ccc;
    cursor: pointer;
  }
  
  .menu-container li:last-child {
    border-bottom: none;
  }
  .menu-container li a{color: #000;}
  </style>
  